.firsb {
  position: relative;
  background: white; }
  .firsb:after {
    content: "";
    position: absolute;
    background: url("../img/first.jpg") center/cover no-repeat;
    height: 100vh;
    width: 50vw;
    right: 0;
    top: 0; }
    @media screen and (max-width: 768px) {
      .firsb:after {
        content: none; } }
  @media screen and (max-width: 768px) {
    .firsb {
      text-align: center; } }
  .firsb__title {
    font-size: 48px;
    color: black;
    max-width: 450px; }
    @media screen and (max-width: 1200px) {
      .firsb__title {
        font-size: 35px; } }
    @media screen and (max-width: 768px) {
      .firsb__title {
        max-width: 100%; } }
  .firsb__desc {
    font-size: 16px;
    line-height: 160%;
    color: black;
    font-family: "Circe";
    max-width: 400px;
    margin-top: 83px;
    margin-bottom: 70px; }
    @media screen and (max-width: 1200px) {
      .firsb__desc {
        margin: 50px 0; } }
    @media screen and (max-width: 768px) {
      .firsb__desc {
        max-width: 100%; } }
  .firsb__wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    min-height: 100vh;
    position: relative; }
  .firsb__block {
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
    position: absolute;
    max-width: 50%;
    padding-right: 20px; }
    @supports (display: flex) {
      .firsb__block {
        transform: none;
        position: static; } }
    @media screen and (max-width: 768px) {
      .firsb__block {
        max-width: 100%;
        padding-right: 0; } }
  .firsb__scroll-down {
    color: black;
    position: absolute;
    font-family: "Circe";
    font-size: 14px;
    font-weight: 300;
    text-align: center;
    bottom: 47px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1; }
    .firsb__scroll-down:after {
      content: "";
      position: absolute;
      display: block;
      width: 11.3px;
      height: 11.3px;
      border-bottom: 1px solid black;
      border-right: 1px solid black;
      transform: rotate(45deg);
      right: 0;
      left: 0;
      margin: 0 auto; }
